<template>
  <div class="login_bg">
    <div class="login_div">
      <div class="login_img_div">
        <img :src="userImg" alt="头像" class="login_img" />
      </div>
      <form></form>
      <div class="login_input">
        <input
          type="text"
          v-model="username"
          placeholder="用户名/邮箱/手机号"
        />
        <input
          type="password"
          placeholder="请输入密码"
          v-model="password"
          show-password
        />
        <button class="login_btn dl" @click="login">登录</button>
        <span class="wanjimima">忘记密码</span>
        <button type="button" class="login_btn zc">注册</button>
      </div>
      <!-- <div class="login_wwq">
        <el-avatar class="wwq" :size="50" :src="userImg"></el-avatar>
        <el-avatar class="wwq" :size="50" :src="userImg"></el-avatar>
        <el-avatar class="wwq" :size="50" :src="userImg"></el-avatar>
      </div> -->
    </div>
  </div>
</template>

<script>
import service from "../utils/request";
export default {
  name: "Login",
  data() {
    return {
      userImg: require("../assets/images/user.jpg"),
      username: "",
      password: "",
      token: "",
      message: "",
    };
  },
  methods: {
    login() {
      
      if (this.username && this.password) {
        //   登录验证
        let par = {
          name: this.username,
          password: this.password,
        };
        service
          .post("/admin/login", par)
          .then((result) => {
            this.token = result.data.token;
            this.message = result.data.msg;
            this.loginmsg();
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        this.$message.error("用户名和密码不为空!");
      }
    },
    loginmsg() {
      if (this.message == "登录成功") {
        //  纪录信息
        // console.log("token:", this.token);
        localStorage.setItem("token", this.token);
        this.$message({
          message: "登录成功",
          type: "success",
        });
        this.$router.push("/admin"); //跳转路由
      } else {
        this.$message.error(this.message + "!");
      }
    },
  },
};
</script>

<style scoped>
