  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  bottom: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(rgb(118, 194, 228), rgb(252, 251, 171));
}
h2 {
  color: #fff;
  margin-bottom: 20px;
}
